.product-list-container {
  max-width: 1000px;
  margin: 20px auto;
}

.add-btn {
  background: linear-gradient(135deg, var(--success), color-mix(in oklab, var(--success) 75%, #0b3));
  color: var(--accent-contrast);
  padding: 10px 16px;
  border: none;
  border-radius: 6px;
  margin-bottom: 20px;
  cursor: pointer;
  transition: 0.3s;
}

.add-btn:hover {
  filter: brightness(0.95);
}

.product-grid {
  display: grid;
  grid-template-columns: repeat(auto-fill, minmax(220px, 1fr));
  gap: 20px;
}

.product-card {
  background: var(--card);
  padding: 15px;
  border-radius: 8px;
  box-shadow: var(--shadow-soft);
  text-align: center;
  border: 1px solid var(--border);
}

.product-card img {
  width: 100%;
  height: 150px;
  object-fit: contain;
}

/* Search and Sort */
.search-sort {
  display: flex;
  justify-content: space-between;
  margin-bottom: 20px;
  gap: 10px;
}

.search-sort input {
  flex: 1;
  padding: 8px 10px;
  border: 1px solid var(--field-border);
  border-radius: 6px;
  font-size: 14px;
  transition: border-color 0.3s;
  background: var(--field-bg);
  color: var(--text);
}

.search-sort input:focus {
  border-color: var(--accent);
  outline: none;
}

.search-sort select {
  padding: 8px 10px;
  border: 1px solid var(--field-border);
  border-radius: 6px;
  font-size: 14px;
  cursor: pointer;
  transition: border-color 0.3s;
  background: var(--field-bg);
  color: var(--text);
}

.search-sort select:focus {
  border-color: var(--accent);
  outline: none;
}

html[data-theme="dark"] .product-card {
  box-shadow: var(--card-shadow-strong);
}

